<template lang="html">
  <div class="royal">
    <top-header></top-header>
    <el-menu
          :default-active="defaultLink"
          class="el-menu-vertical"
          :router="true"
          background-color="#545c64"
          text-color="#fff"
          @open="handleOpen"
          @close="handleClose">
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-menu"></i>
              <span>忠诚度模块</span>
            </template>
            <el-menu-item v-if="!menuItem.children" :index="menuItem.linkName" v-for="(menuItem,index) in menuKeywords" :key="menuItem.linkName">
              <template slot="title">
                <!-- <i :class="menuItem.icon"></i> -->
                <span>{{menuItem.title}}</span>
              </template>
            </el-menu-item>
            <!-- 子类 -->
            <el-submenu v-if="menuItem.children" :index="menuItem.linkName" v-for="(menuItem,index) in menuKeywords" :key="menuItem.linkName">
              <template slot="title">
                <!-- <i :class="menuItem.icon"></i> -->
                <span>{{menuItem.title}}</span>
              </template>
                <el-menu-item v-for="(item,index) in menuItem.children" :index="item.linkName" :key="item.title">
                  <i :class="item.icon"></i>
                  <span>{{item.title}}</span>
                </el-menu-item>
            </el-submenu>
      </el-submenu>

      </el-menu>

      <div class="content-wrapper">
          <router-view></router-view>
      </div>

  </div>
</template>

<script>
// title:'操作结果',
// linkName:'/operateRes',
import TopHeader from '@/components/base/topFixedHeader'
export default {
  components:{
    TopHeader
  },
  data() {
    return {
      menuKeywords:[{
        title:'主规则',
        linkName:'/mainRule',
      },{
        title:'规则配置',
        linkName:'/ruleConfig',
        children:[{
          icon:"el-icon-undefined iconfont icon-user",
          title:'会员',
          linkName:'/ruleConfig/member',
        },{
          icon:"el-icon-undefined iconfont icon-wenjuandiaocha",
          title:'订单',
          linkName:'/ruleConfig/order',
        },{
          icon:"el-icon-undefined iconfont icon-biaoqianguanli1",
          title:'标签',
          linkName:'/ruleConfig/tag',
        }]
      },{
        title:'参数配置',
        linkName:'/paramConfig',
        children:[{
          icon:"el-icon-document",
          title:'规则参数',
          linkName:'/paramConfig/rule',
        },{
          icon:"el-icon-star-off",
          title:'奖励参数',
          linkName:'/paramConfig/reward',
        }]
      }]
    }
  },

  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  },
  created() {
    console.log(this.$route)
  },
  computed:{
    defaultLink() {
      return this.$route.path
    }
  }
}
</script>

<style lang="stylus" scoped>
.royal
  display flex;
  height 100%
  padding-top 50px
  .el-menu-vertical
    height 100%
    flex 200px 0 0
  .content-wrapper
    flex:1

</style>



